        <!--                default-active="1"-->
        <!--                @select="handelSelect">-->
        <!--            <el-menu-item class="el-col-3 el-col-push-1" index="0">-->
        <!--                <div style="height: 100%;width: 100%;">-->
        <!--                    <svg width="121" height="52" xmlns="http://www.w3.org/2000/svg"-->
        <!--                         xmlns:xlink="http://www.w3.org/1999/xlink">-->
        <!--                        <g>-->
        <!--                            <title>background</title>-->
        <!--                            <rect fill="#fff" id="canvas_background" height="54" width="123" y="-1" x="-1" />-->
        <!--                            <g display="none" overflow="visible" id="canvasGrid">-->
        <!--                                <rect fill="url(#gridpattern)" stroke-width="0" y="0" x="0" height="100%"-->
        <!--                                      width="100%" />-->
        <!--                            </g>-->
        <!--                        </g>-->
        <!--                        <g>-->
        <!--                            <title>Layer 1</title>-->
        <!--                            <image xlink:href="data:image/png;base64,"-->
        <!--                                   id="svg_1" height="63.23905" width="143.00001" y="-5.0996" x="-9.00001" />-->
        <!--                        </g>-->
        <!--                    </svg>-->
        <!--                </div>-->
        <!--            </el-menu-item>-->

        <!--            <el-menu-item class="el-col-2 el-col-push-4" index="1">{{indexText}}</el-menu-item>-->
        <!--        </el-menu>-->
        <el-page-header style="margin-left: 2vw;line-height: 70px" @back="$router.push('/')"
                        :content="indexText"></el-page-header>

        <div style="margin-top: 1vh;width: 8.32vw;height: 10vh;">
            <el-popconfirm
                    confirmButtonText='退出'
                    cancelButtonText='取消'
                    icon="el-icon-info"
                    iconColor="red"
                    @onConfirm="logOut"
                    title="退出登录？">
                <el-avatar
                        slot="reference"
                        src="https://myblog-figure-bed-1258528016.cos.ap-guangzhou.myqcloud.com/%E5%85%B6%E4%BB%96/%E9%BB%91%E4%BA%BA%E6%8A%AC%E6%A3%BA.png"></el-avatar>
            </el-popconfirm>
            <p>{{userData.userName}}</p>
        </div>
        <div>
            <div class="menuBar el-col-2">
                <el-menu
                        default-active="1"
                        @select="changeMenu"
                        class="el-menu-vertical-demo">
                    <el-menu-item index="1">
                        <i class="el-icon-user"></i>
                        <span slot="title">个人资料</span>
                    </el-menu-item>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">我的收藏</span>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-document"></i>
                        <span slot="title">学生认证</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <i class="el-icon-setting"></i>
                        <span slot="title">账号设置</span>
                    </el-menu-item>
                </el-menu>
            </div>
            <div v-show="indexText=='个人资料'">
                <el-card class="box-card-person">
                    <div slot="header" class="clearfix">
                        <span>{{userData.userName}},下午好</span>
                        <el-button style="float: right; padding: 3px 0" type="text" @click="logOut">退出登录</el-button>
                    </div>
                    <el-menu default-active="1" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                        <el-menu-item index="1">基本资料</el-menu-item>
                        <el-menu-item index="2">修改头像</el-menu-item>
                        <el-menu-item index="3">密码管理</el-menu-item>
                    </el-menu>
                </el-card>
                <div class="line"><br></div>

                <el-card v-show="String(currentIndexOfUserInfo)==='1'" class="box-card-person">
                    <div class="text item" style="text-align: left">
                        用户名: {{userData.userName}}<br>
                        手机号: {{userData.userPhone}}<br>
                        是否学生认证: {{userData.studentAuthentication==='N'?'否':'是'}}
                    </div>
                </el-card>

            </div>
            <div v-show="indexText=='我的收藏'">
                这里什么都没有哦
            </div>
            <div v-show="indexText=='学生认证'">
                这里什么都没有哦
            </div>
            <div v-show="indexText=='账号设置'">
                这里什么都没有哦
            </div>
        </div>
        <Footer />
    </div>
</template>

<script>
import Footer from '../components/Footer'

export default {
  name: 'userData',
  components: {Footer},
  data () {
    return {
      indexText: '个人资料',
      // 用户登录标识
      loginCookie: '',
      currentIndexOfUserInfo: 1,
      // 用户信息
      userData: {
        // 用户头像url
        userHead: '',
        // 昵称
        userName: '',
        // 手机号
        userPhone: '',
        // 是否学生认证
        studentAuthentication: false
      }
    }
  },
  watch: {},
  methods: {
    handleSelect (index, indexPath) {
      this.currentIndexOfUserInfo = index
    },
    /*
      * 左边的菜单栏变化
      */
    changeMenu (index, indexPath) {
      switch (index) {
        default:
        case '1':
          this.indexText = '个人资料'
          break
        case '2':
          this.indexText = '我的收藏'
          break
        case '3':
          this.indexText = '学生认证'
          break
        case '4':
          this.indexText = '账号设置'
          break
      }
    },
    /*
      * 退出登录：清除本地 cookie；调用后台接口
      */
    logOut () {
      const that = this
      that.$http.get('/i/logout', {
        params: {
          Cookie: this.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (response.data.errorCode === 200) {
            that.$notify.success({
              title: '注销成功',
              message: '您的账号已退出登录'
            })
            // 删除 token
            that.$cookie.delete('token')
            // 删除 userData
            that.$cookie.delete('userData')
            that.userData = ''
            that.isLogin = false
          } else {
            that.$notify.error({
              title: '注销失败',
              message: response.data.errorMessage
            })
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          that.$router.push('/refreshIndex')
        })
    }
  },
  created () {
    const that = this
    if (that.$cookie.get('token')) {
      that.loginCookie = that.$cookie.get('token')
      // 请求获取个人信息
      that.$http.get('/i', {
        params: {
          Cookie: that.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (response.status === 200) {
            console.log(response.data.errorCode + '\t' + response.data.errorMessage)
            if (response.data.errorCode !== 4103) {
            // 将请求到的数据写到用户信息中
              that.userData = response.data.data
              console.log(that.userData)
            }
          } else {

          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
        // always executed
        })
    } else {
      that.$router.push('/login')
    }
  }
}
</script>

<style scoped>
    .text {
        font-size: 17px;
        line-height: 30px;
        font-family: DengXian_Reg, serif;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }

    .clearfix:after {
        clear: both
    }

    .box-card-person {
        width: 55vw;
        margin-left: 25vw;
    }
</style>



